<template>
    <div id="wrapper" ref="myscroll">
        <slot></slot>
    </div>
</template>

<script>
import IScroll from 'iscroll/build/iscroll-probe'

export default {
    name: 'Scrollview',
    mounted() {
			this.iscroll = new IScroll(this.$refs.myscroll,{
				mouseWheel: true,
				scrollbars: false,
				scrollX:false,
				scrollY:true,
				disableMouse: false,
				disablePointer: false,
				disableTouch:false,
				preventDefault:false, // iscroll
				probeType:3,
				onClick:true,
			})
			// 1. 
			let observer = new MutationObserver((mutationList, observer)=>{
				this.iscroll.refresh();
			});
			// 2. 
			let observerOptions = {
				childList: true,  // 
				attributeFilter: ['height'], // 
				subtree: true     //  false
			};
			// 3. ,
			observer.observe(this.$refs.myscroll, observerOptions);

			
		},
		methods:{
			//   
			scrolling(fn){
				this.iscroll.on('scroll', function(){
					fn(this.y);
				});
			},
			refresh(){
				setInterval(()=>{
					this.iscroll.refresh();
				},200);
			},
			Toscrolling(x, y, time){
				this.iscroll.scrollTo(x, y, time);
			}
		}
}
</script>

<style lang="scss">
	#wrapper{
		width: 100%;
		height: 100%;
	}
</style>